body {
    background-color: #181818;
    color: #F0F0F0;
    height: 100%;
    font-family: 'Lato', sans-serif;
  }
  
  a {
    color: #00B3E5;
    text-decoration: underline;
  }
  
  .chatbot-container {
    background-color: #181818;
    color: #F0F0F0;
    width: 50%;
    height: 75%;
    border: 2px solid #00B3E5;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 600px;
  }
  
  .chatbot-messages {
    flex-grow: 1;
    overflow: auto;
    min-height: 50%;
    background-color: rgba(22, 21, 21, 0.3);
  }
  
  .chatbot-message {
    margin-bottom: 10px;
    color: #F0F0F0;
  }
  
  .chatbot-message.user {
    background-color: #181818;
    color: #00B3E5;
    border: 2px solid #00B3E5;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .chatbot-message.bot {
    justify-content: flex-start;
    align-items: flex-start;
    padding: 10px;
  }
  
  
  form {
    display: flex;
    background-color: #00B3E5;
    padding: 5px;
  }
  
  textarea {
    flex-grow: 1;
    margin-right: 5px;
    border: none;
    background-color: #181818;
    color: #00B3E5;
    font-size: 16px;
    outline: none;
    border: #474747;
    border-radius: 5px;
  }
  
  button[type="submit"] {
    background-color: #181818;
    color: #00B3E5;
    font-size: 16px;
    border: none;
    cursor: pointer;
    outline: none;
  }
  .lds-ripple {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 40px;
  }
  .lds-ripple div {
    position: absolute;
    border: 2px solid #fff;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  .chatbot-footer {
    font-size: 14px;
    padding: 5px;
  }
  .chatbot-header {
    padding: 10px;
  }
  @keyframes lds-ripple {
    0% {
      top: 18px;
      left: 18px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 18px;
      left: 18px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 18px;
      left: 18px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 36px;
      height: 36px;
      opacity: 0;
    }
  }
  


  @media screen and (max-width: 600px) {
    /* Adjust chatbot container */
    .chatbot-container {
      width: 90%;
      height: 100%;
      margin: auto;
      position: static;
      transform: none;
      max-width: 400px;
    }
  
    /* Adjust chatbot messages */
    .chatbot-messages {
      min-height: 50%;
      flex-grow: 1;
      overflow: auto;
    }
  }

